import React from 'react'
import { Link } from 'react-router-dom';
import screen1 from './img_NinjaShadowOfDarkness/ss1.jpg'
import screen2 from './img_NinjaShadowOfDarkness/ss2.jpg'
import screen3 from './img_NinjaShadowOfDarkness/ss3.jpg'
import screen4 from './img_NinjaShadowOfDarkness/ss4.jpg'
import screen5 from './img_NinjaShadowOfDarkness/ss5.jpg'
import screen6 from './img_NinjaShadowOfDarkness/ss6.jpg'
import screen7 from './img_NinjaShadowOfDarkness/ss7.jpg'
import logo from './img_NinjaShadowOfDarkness/logo.png'
import "../../style/components/ninjaShadowOfDarknessPage.css"

const NinjaShadowOfDarknessPage = () => {

    return (
        <div>
            <div className='artykul'>
                <img id='logoCBF' src={logo} alt={"rdo: https://images.launchbox-app.com/3298b017-6ef5-4844-bcc4-a22e8db5da81.png"} title={"Tak wyglda logo gry"}></img>
                <p lang='pl'><abbr title='Inny gra studia, ktra odgrywaa si w czasach wspczesnych'>Fighting Force</abbr> w feudalnej Japonii z elementami platformowymi wycignitymi z pierwszego <abbr title='Przejd do recenzji pierwszego Crasha'><Link to='../crashbandicoot'>Crasha</Link></abbr>, gdzie prowadzisz wojownika Ninja do walki przeciwko smokom lub demonicznym plugawstwom  tak mog opisa w jednym zdaniu <b>Ninja: Shadow of Darkness</b> od Core Design</p>
                <img className='image' src={screen1} alt={"rdo: https://psxdatacenter.com/games/P/N/SLES-01554.html"} title={"Najgorsze poczenie: wski most i duo przeciwnikw"}></img>
                <p lang='pl'>Bardzo dugo opieraem si tej grze, gdy utkwia ona w mej pamici jako bardzo wymagajc. Po tylu latach, majc za sob ukoczone gry od <abbr title='Twrcy takiej serii jak Dark Souls lub gier jak: Bloodborne czy Elden Ring'>From Software</abbr>, byem przekonany e jestem na ni gotowy okazao si jednak e to bya bardzo nieprzemylana decyzja. Gra jest piekielnie trudna, wymagajca i wyciskajca z nas sidme poty. Nie wybacza ona bdw i wrcz wymaga od nas chirurgicznej precyzji w przechodzeniu plansz</p>
                <img className='image' src={screen2} alt={"rdo: https://psxdatacenter.com/games/P/N/SLES-01554.html"} title={"Maa platforma, miercionocina woda wkoo... czego chcie wicej?"}></img>
                <p lang='pl'>Problemem nie jest tutaj system walki, gdy opiera si on na dwch przyciskach, odpowiadajcych za ataki piciami lub nogami. Mamy te blok i dwa dodatkowe ataki specjalne. Poza tym, mamy jeszcze system zwikszania siy atakw i czarw poprzez specjalne, poukrywane po planszach zwoje. Oczywicie moemy znale bronie, ktre bardzo uatwiaj nam ubijanie czortw. Jak jestemy leniwi, to po kadej planszy moemy za zebrane monety kupi bro, dodatkow moc lub ycia.</p>
                <img className='image' src={screen3} alt={"rdo: https://psxdatacenter.com/games/P/N/SLES-01554.html"} title={"W sklepie moemy sobie kupi udko, bro i cenne ycie"}></img>
                <p lang='pl'>Niby bardzo fajna opcja, ale wystarczy tylko jeden bd, jedno potknicie lub zbyt dua ilo wrogw naraz, a nasze zebrane lub kupione bronie, wraz z jednym poziomem mocy, przepadaj bezpowrotnie, co niestety powoduje e przeciwnikw ubijamy duej, a to z kolei czyni gr trudniejsz. <b>Czyli ginc raz, giniemy czciej, a ginc czciej gra robi si nie do przejcia.</b> (w pniejszych poziomach)</p>
                <img className='image' src={screen4} alt={"rdo: https://psxdatacenter.com/games/P/N/SLES-01554.html"} title={"Ciemno wszdzie, gucho wszdzie... przeciwnikw peno"}></img>
                <p lang='pl'>Plansze s bardzo urozmaicone, gdy przyjdzie nam si zmaga z wrogami w deszczowych lasach, mrocznych jaskiniach, sonecznych plaach, na piekielnych otchaniach zalanych law koczc. Kade maj swj unikatowy klimat i zestaw przeciwnikw. Poza tym, na deser, mamy na kocu kadego <abbr title='inaczej plansza, poziom'>levelu</abbr> walk z <abbr title='ostatni przeciwnik, zazwyczaj bardzo trudny'>bossem</abbr>. Ich trudno jest rna, niektrych moemy zgadzi zwykym <abbr title='inaczej, bardzo szybkie przyciskanie przycisku'>mashowaniem</abbr> przycisku ataku, a pozostali wymagaj od nas nauczenia si ich ruchw, bymy z tej walki wyszli cao.</p>
                <img className='image' src={screen5} alt={"rdo: https://psxdatacenter.com/games/P/N/SLES-01554.html"} title={"Wydepilowany Tauren stoi nam na drodze ukoczenia gry"}></img>
                <p lang='pl'>Graficznie ten tytu przedstawia si bardzo sympatycznie, levele s bardzo adnie wymodelowane i zaprojektowane. cieka dwikowa buduje bardzo skutecznie nastrj na zabijanie przeciwnikw, zwiedzanie i podziwianie wiata wykreowanego przez twrcw.</p>
                <img className='image' src={screen6} alt={"rdo: https://psxdatacenter.com/games/P/N/SLES-01554.html"} title={"Panowie nie chc ze mn lepi bawana"}></img>
                <p lang='pl'><b>Gra wymczya mnie niezmiernie!</b> Napis Game Over im dalej w las, tym pojawia si coraz czciej. y nie uda nam si wyfarmi zbyt duo, bo gra ma limit posiadania ich tylko dziewiciu i (chyba) czterech <abbr title='czyli opcja kontynuowania gry, z utrat wszystkich postpw poza lokalizacj'>Continue</abbr>. Sposobem na przejcie Ninja: Shadow of Darkness, jest nauczenie si plansz i znajdowanie sekretw, ktre chowaj si za specjalnymi cianami lub w ukrytych przejciach. Po kilkunastu godzinach udao mi si ukoczy ten tytu. Dokonaem tego przechodzc go w sposb nastpujcy - staraem si koczy level, nie tracc w nim ycia (byo to niemoliwe ju na trzech ostatnich poziomach). Zadanie to byo bardzo trudne, ale uwierzcie mi, na ostatniej planszy oszczdno y, si opacia, bo kocowy boss by niemoliwie trudny i wyrywaem sobie na nim wosy</p>
                <img className='image' src={screen7} alt={"rdo: https://psxdatacenter.com/games/P/N/SLES-01554.html"} title={"Walka parawianw na play nie oszczdzia rwnie tej gry"}></img>
                <p>Niestety po zakoczeniu gry wiksz satysfakcj czerpaem z samego faktu jej ukoczenia, ni z kocowej bardzo skromnej animacji.</p>
                <div className='tabela'>
                    <table id='tabelaInfo'>
                        <tr>
                            <td id='pierwszaKolumna'>Deweloper</td>
                            <td>Core Designe</td>
                        </tr>
                        <tr>
                            <td id='pierwszaKolumna'>Wydawca</td>
                            <td>Eidos</td>
                        </tr>
                        <tr>
                            <td id='pierwszaKolumna'>Data wydania</td>
                            <td>30 wrzenia 1998</td>
                        </tr>
                        <tr>
                            <td id='pierwszaKolumna'>Numer Seryjny</td>
                            <td>SLES-01554</td>
                        </tr>
                    </table>
                </div>
                <div>
                    {/* <img></img>
                <img></img> */}
                </div>
            </div>
        </div>
    )
}

export default NinjaShadowOfDarknessPage