import React, { useState } from 'react'
import screen1 from './img_CrashBandicoot/ss16.jpg'
import screen2 from './img_CrashBandicoot/ss20.jpg'
import screen3 from './img_CrashBandicoot/ss6.jpg'
import screen4 from './img_CrashBandicoot/ss7.jpg'
import screen5 from './img_CrashBandicoot/ss1.jpg'
import screen6 from './img_CrashBandicoot/ss2.jpg'
import screen7 from './img_CrashBandicoot/ss3.jpg'
import logo from './img_CrashBandicoot/logo_CrashBandicoot.png'
import "../../style/components/crashBandicootPage.css"

const CrashBandicootPage = () => {

    return (
        <div>
            <div className='artykul'>
                <img id='logoCBF' src={logo} alt={"rdo: samemu okroiem z okadki"} title={"Tak wyglda logo gry"}></img>
                <p lang='pl'>Mona j z atwoci przej w dwie godziny, ale zdobycie stu procent wymaga nie lada odwagi, wytrzymaoci oraz szczcia. Tak oto widz pierwsz produkcj studia Naughty Dog na PlayStation.</p>
                <img className='image' src={screen1} alt={"rdo: https://psxdatacenter.com/games/P/L/SLES-00613.html"} title={"Ta plansza niszczya dziecistwo"}></img>
                <p lang='pl'>T odson charakteryzuje to, e mona j jednoczenie kocha i nienawidzi. Gra jest pikna! W 1996 roku na PlayStation nie wyszo duo platformwek w trzech wymiarach np. Bubsy 3D, Pandemonium! czy Cheesy, ktre wyszy rnie jak i wyglday inaczej. Poza tym, inne tytuy z tego roku zostay przez Crasha zmiadone jak tytuowy jamraj przez kamienne bloki. Zasad tej gry jest pokonanie wszystkich plansz na trzech wyspach, nie tracc przy tym wszystkich y.</p>
                <img className='image' src={screen2} alt={"rdo: https://psxdatacenter.com/games/P/L/SLES-00613.html"} title={"To by Hulk mojego dziecistwa"}></img>
                <p lang='pl'>Plansze s rwnie ciekawe jak sama oprawa graficzna. Przyjdzie nam si tutaj wspina po ogromnych palisadach wypenionych przeciwnikami, bdziemy ucieka przed toczc si kul, przemierza lene cieki, zaronite zowrogimi rosiczkami, laboratoria zego naukowca ktre s wypenione po brzegi robotami i laserami, przemierza w chmurach dziurawe, drewniane mosty lub wspina si po cianie zamczyska w trakcie burzy, podczas, gdy krople deszczu zalewaj kamienne bloki, czynic je bardzo liskimi. Wszystkie te plansze s bardzo mdrze poukadane i kada z nich ma w sobie pewnego rodzaju wyzwanie.</p>
                <img className='image' src={screen3} alt={"rdo: https://psxdatacenter.com/games/P/L/SLES-00613.html"} title={"Inni nienawidzili plansz rzecznych, ja kocham do dzisiaj"}></img>
                <p lang='pl'>Jak wspomniaem w pierwszym akapicie, gr mona ukoczy bez wysiku w 2h, ale prawdziwa zabawa zaczyna si przy prbie przejcia jej w peni. Wyzwaniem jest zniszczenie wszystkich skrzy na planszy i w bonusowych ich pomieszczeniach, nie tracc przy tym ani jednego ycia. Dodajcie do tego jeszcze sterowanie, do ktrego trzeba przywykn, wraz z du iloci skakania, po czasami bardzo wymagajcych, platformach.</p>
                <img className='image' src={screen4} alt={"rdo: https://psxdatacenter.com/games/P/L/SLES-00613.html"} title={"Scena jak z animowanego Indiany Donsa"}></img>
                <p lang='pl'>Za kad udan prb przejcia poziomu bonusowego, od naszej dziewczyny Tawny, ktrej swoj drog uratowanie jest naszym gwnym celem w grze, otrzymujemy moliwo jej zapisania (gry, nie dziewczyny) na karcie pamici lub otrzymujemy sekwencj przyciskw do wczytania. Zapisa gr moemy jeszcze w momencie gdy zbierzemy jaki kryszta, ktry otrzymujemy za zniszczenie wszystkich skrzy. Wydaje si to atwe - bynajmniej - nie jest na ostatniej, trzeciej wyspie. Przykadowo wszystkim znany poziom Slippery Climb ma w sobie bodaje tylko dwa? <abbr title='Punkty zapisu lub miejsce do ktrego wrcimy po mierci'>Checkpointy</abbr> porozrzucane przy kadym kocu ciany. </p>
                <img className='image' src={screen5} alt={"rdo: Wasne"} title={"Tak! Mam kryszta w najtrudniejszej planszy"}></img>
                <p lang='pl'>eby tego byo mao, gra te uniemoliwia nam nazbieranie sobie sporego zasobu y, gdy samo zapisanie gry nie zapamituje ich. Po kadym wczytaniu gry, liczba y spada do czterech i musimy zazwyczaj wraca do pierwszych plansz, aby je na nowo nazbiera.</p>
                <p>Jak wida kultowa produkcja Naughty Dog si z nami nie cacka i cay czas rzuca kody pod nogi, jak na planszy w starych ruinach. Moemy oczywicie nabra wprawy i jak na przykad ja (skromnym zdaniem), przechodzi niektre levele bez problemu, po prostu pamitajc, co nas czeka zawczasu.
                </p>
                <img className='image' src={screen6} alt={"rdo: Wasne"} title={"Wida w tle pozostae 3 wysypy"}></img>
                <p lang='pl'>Takie zapamitywanie przydaje si rwnie w walkach z bossami, ktre pojawiaj si co kilka poziomw. Wszyscy maj swj niezmieniony schemat, ktry warto zapamita, eby z atwoci zmniejsza ich paski ycia do zera absolutnego Tak jakby, gdy z czci z nich staniemy w szranki w pniejszych odsonach Crasha.</p>
                <img className='image' src={screen7} alt={"rdo: Wasne"} title={"Ujedanie dzika, to jest TO!!!"}></img>
                <p>Crash Bandicoot jest tak samo smaczny jak owoce Wumpa, posmakujesz jak zagrasz, mog one by dla Ciebie sodkie, z lekk goryczk lub w ogle niestrawne - wszystko zaley od Twojej tolerancji na wymagajce gry, cierpliwoci i chci uczenia si schematw, poprzez wielokrotne ich powtarzanie.</p>
                <div className='tabela'>
                    <table id='tabelaInfo'>
                        <tr>
                            <td>Deweloper</td>
                            <td>Naughty Dog</td>
                        </tr>
                        <tr>
                            <td>Wydawca</td>
                            <td>Sony</td>
                        </tr>
                        <tr>
                            <td>Data wydania</td>
                            <td>15 listopada 1996</td>
                        </tr>
                        <tr>
                            <td>Numer Seryjny</td>
                            <td>SCES-00344</td>
                        </tr>
                    </table>
                </div>
                <div>
                    {/* <img></img>
                <img></img> */}
                </div>
            </div>
        </div>
    )
}

export default CrashBandicootPage