import React from 'react'
import logo from './img_CrashBandicoot/logo_CrashBandicoot.png'
import "../../style/pages/crashBandicootShort.css"
import { useNavigate } from 'react-router-dom'

const CrashBandicootShort = () => {

    const navigate = useNavigate();

    return (
        <div className='shortArticle'>
            <div className='logoContainerCB'>
                <img id='logoCB' src={logo} alt={"rdo: samemu okroiem z okadki"} title={"Tak wyglda logo gry"}></img>
            </div>
            <div>
                <div>
                    <p lang='pl' id='wstep'>Mona j z atwoci przej w dwie godziny, ale zdobycie stu procent wymaga nie lada odwagi, wytrzymaoci oraz szczcia. Tak oto widz pierwsz produkcj studia Naughty Dog na PlayStation.</p>
                </div>
                <div id='goToFullArticleButton' onClick={() => navigate(`/artykuly/crashbandicoot`)}>Przejd do Artykuu</div>
            </div>
        </div>
    )
}

export default CrashBandicootShort