import React from 'react'
import logo from './img_LifeForceTenka/LifeforceTenkaLogo.png'
import "../../style/pages/lifeForceShort.css"
import { useNavigate } from 'react-router-dom'

const LifeForceTenkaShort = () => {

    const navigate = useNavigate();

    return (
        <div className='shortArticle'>
            <div className='logoContainer'>
                <img id='logo' src={logo} alt={"rdo: samemu okroiem z okadki"} title={"Tak wyglda logo gry"}></img>
            </div>
            <div>
                <div>
                    <p lang='pl' id='wstep'>Strzelanka na PlayStation, ktr pamitaem z Dema i zawsze chciaem zagra w jej pen wersj. Po jej zakupie po 20-tu latach, stwierdziem e niepotrzebnie tyle zwlekaem, tak jakby...</p>
                </div>
                <div id='goToFullArticleButton' onClick={() => navigate(`/artykuly/lifeforcetenka`)}>Przejd do Artykuu</div>
            </div>
        </div>
    )
}

export default LifeForceTenkaShort