import screen4 from './img_LifeForceTenka/ss13.jpg'
import screen5 from './img_LifeForceTenka/ss8.jpg'
import screen6 from './img_LifeForceTenka/ss4.jpg'
import logo from './img_LifeForceTenka/LifeforceTenkaLogo.png'
import "../../style/components/lifeForceTenkaPage.css"

const LifeForceTenkaPage = () => {

    // const [showArticle, setShowArticle] = useState();

    // const toggleShowArticle = () => {
    //     setShowArticle(!showArticle);
    // }

    // const buttonText = showArticle ? "Czytaj mniej" : "Czytaj wicej";

    // const readMore = () => {
    //     let breakPoint = document.getElementById("breakPoint");
    //     let more = document.getElementById("more");

    //     if (breakPoint.style.display === "none") {
    //         breakPoint.style.display = "inline";
    //         more.style.display = "none";
    //         toggleShowArticle()
    //     } else {
    //         breakPoint.style.display = "none";
    //         more.style.display = "inline";
    //         toggleShowArticle()
    //     }
    // }

        return (
            <div>
                <div className='artykul'>
                    <img className='image' src={logo} alt={"rdo: samemu okroiem z okadki"} title={"Tak wyglda logo gry"}></img>
                    <p lang='pl' id='wstep'>Strzelanka na PlayStation, ktr pamitaem z Dema i zawsze chciaem zagra w jej pen wersj. Po jej zakupie po 20-tu latach, stwierdziem e niepotrzebnie tyle zwlekaem, tak jakby...</p>
                    <img className='image' src={screen1} alt={"rdo: https://psxdatacenter.com/games/P/L/SLES-00613.html"} title={"Tak wyglda autocelowanie w grze"}></img>
                    <p lang='pl'>Fabua w tej grze jest tylko zapychaczem, ktry wyranie pokazuje, e twrcy wstawili j chyba troch dla artw. Zakoczenie jest rozczarowujce, gdy po tym co przeywamy, chciaoby si zobaczy co wicej, ni marne kilka sekund.</p>
                    <img className='image' src={screen2} alt={"rdo: https://psxdatacenter.com/games/P/L/SLES-00613.html"} title={"Widzisz tak przestrze zazwyczaj w 10 fps-ach"}></img>
                    <p lang='pl'>Moecie si domyla, e Tenka nie naley do atwych gier, niestety (albo stety) jest to tytu BARDZO wymagajcy. Trudno tutaj objawia si w kilku elementach: celowanie, detekcja strzaw i irytujcy przeciwnicy. Gra niestety nie obsuguje <abbr title='PlayStation 1, miaa moliwo podczenia dedykowanej myszy'>myszy</abbr>, wic twrcy postanowili wprowadzi autocelowanie. Wyszo to moim zdaniem miernie, gdy nie zawsze to dziaa. No i jeszcze kwestia samego strzelania, ktre tutaj, mam wraenie, w wikszoci nie dziaa.</p>
                    <p>Raz zabicie tego samego wroga odbywa si po wystrzeleniu kilku pociskw, a innym razem potrzeba kilkudziesiciu. Strasznie to irytuje, gdy mamy bardzo ograniczon liczb amunicji. To co zasuguje za uznanie, to mechanika odstrzeliwania koczyn lub gw przeciwnikw, ale dodajc do tego autocelowanie (zawsze) w tors przeciwnika, to moecie si domyle, e na <abbr title='Inaczej kursory lub krzyak'>D-Padzie</abbr> sadzenie headshotw jest problematyczne.</p>
                    <img className='image' src={screen3} alt={"rdo: https://psxdatacenter.com/games/P/L/SLES-00613.html"} title={"Jak ten skurczybyk jest tak blisko, to ty jeste rwnie blisko mierci"}></img>
                    <p lang='pl'>Kolejnym tematem s oczywicie przeciwnicy, ktrzy s rnorodni i nierzadko nawet przeraajcy. Znajdziemy tutaj mae latajce drony oraz roboty, mamy humanoidalne stwory i oczywicie abominacje typu gowy na dwch koczynach, fruwajce ogromne mzgi itd. Najwicej problemu sprawiaj mali przeciwnicy gdy s bardzo szybcy i trafienie ich graniczy z cudem. Ci wiksi natomiast maj w swoim arsenale midzy innymi wyrzutnie rakiet - wystarczy jednego z nich zignorowa, a bardzo szybko przekonamy si, e w tej grze nie ma <abbr title='Punkty zapisu lub miejsce do ktrego wrcimy po mierci'>checkpointw</abbr> i bdziemy zaczyna <abbr title='Poziom lub mapa na ktrej aktualnie gramy'>level</abbr> od nowa.</p>
                    <img className='image' src={screen4} alt={"rdo: https://psxdatacenter.com/games/P/L/SLES-00613.html"} title={"Mzg rozwin skrzyda... znaczy koczyny"}></img>
                    <p lang='pl'>Graficznie gra jest obdna, aczkolwiek te "fajerwerki" przypaca pynnoci, ktra tutaj czasami spada do paru klatek na sekund - przy wikszej grupce wrogw, mamy istny pokaz slajdw. Niestety twrcy poszli na atwizn z konstrukcj plansz, gdy niektre s wrcz zdublowane.
                    </p>
                    <p> Rodzaje misji rwnie, co powoduje e zaczynamy si nudzi, kiedy po raz kolejny musimy usun bomby z planszy, lub szuka specjalnych kart.</p>
                    <img className='image' src={screen5} alt={"rdo: https://psxdatacenter.com/games/P/L/SLES-00613.html"} title={"Mrok i ciemne korytarze to tutaj codzienno"}></img>
                    <p lang='pl'>Gra posiada bardzo ciekawy rozwj broni, poniewa mamy jeden karabin, ktry po znalezieniu odpowiedniego moda, ulepsza si i zmienia. Mamy trzy gwne tryby karabinu - jednostrzaowy, podwjny i cigy, a do tego jeszcze dochodz inne bajery, jak laser czy wyrzutnia rakiet.</p>
                    <img className='image' src={screen6} alt={"rdo: https://psxdatacenter.com/games/P/L/SLES-00613.html"} title={"To co mwi: Taki @#uj"}></img>
                    <p lang='pl'>Pomimo tych problemw gra mnie w pewien sposb urzeka, moe to z uwagi na sentyment wyniesiony z obcowania z wersj <abbr title='Wersja prbna gry, gdzie zazwyczaj dostpny jest tylko jeden poziom'>Demo</abbr>. Przyjdzie nam tu pokona kilkanacie poziomw, ktre wrcz wycisn z nas ostatnie resztki cierpliwoci i przetestuj nasz wytrzymao. Ja przechodziem jeden poziom dziennie, gdy ginem na nich tak czsto i obficie, e nie daem rady mentalnie. Widzc kocowe napisy, radowaem si jak dziecko, znajdujce pod choink wymarzony prezent. Jestem z siebie dumny, e udao mi si ten tytu ukoczy.</p>
                    <div>
                        <img></img>
                        <img></img>
                    </div>
                </div>
            </div>
        )

}

export default LifeForceTenkaPage